@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
}

.home,
.reports,
.products,
.team,
.reports {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

nav a div {
  display: flex;
  align-items: center;
}
div.App {
  background-color: rgba(0, 0, 0, 0.3);
  position: relative;
  top: 80px;
}
.output-border {
  border-color: green;
}

.output-text {
  color: purple;
}

.output-image {
  margin: auto; /* Center */
}

html body div#root div.App h1 {
  color: #00ffaeff important;
}

h3,
h4,
h5 {
  color: #17b9cbff important;
}

ol li {
  color: #17b9cbff !important;
  font-size: 1.5rem;
}
.p-4.notebook-class {
  background-color: white;
}
div div > p {
  overflow: scroll;
}

#loader {
  position: absolute;
  top: 50vh;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 350px;
}
.progress-bar {
  width: 0%;
  height: 5px;
  background: linear-gradient(
    to right,
    #4cd969,
    #5ac8fa,
    #0084ff,
    #34aadc,
    #5856d9,
    #ff2d53
  );
  margin-top: 10px;
  background-size: 350px 5px;
  border-radius: 10px;
  -webkit-animation: loading 1s ease-in-out forwards;
          animation: loading 1s ease-in-out forwards;
}

.shadow {
  width: 100%;
  height: 40px;
  background: linear-gradient(
    to bottom,
    rgba(100, 100, 100, 0.17),
    rgba(100, 100, 100, 0.1),
    transparent
  );
  -webkit-transform: skew(45deg) translate(15px, 5px);
          transform: skew(45deg) translate(15px, 5px);
}

@-webkit-keyframes loading {
  to {
    width: 100%;
  }
}

@keyframes loading {
  to {
    width: 100%;
  }
}

div.home {
  text-align: center;
  font-size: clamp(1rem, 10vw, 5rem);
  background-color: white;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.header-content {
  position: fixed;
  top: 0;
  display: block !important;
  z-index: 2;
  width: 100% !important;
}
div a.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
}
div a.header img {
  width: 300px;
  margin-right: 5%;
  z-index: 2;
  overflow: hidden;
  margin-top: 13px;
}

@media only screen and (max-width: 400px) {
  div#root nav {
    width: 250px;
  }
  div#root a span {
    font-size: 12px;
  }
  div.header-content {
    height: 50px;
  }
  div a.header {
    height: 50px;
  }
  div a.header img {
    width: 200px;
    margin-right: 7%;
    overflow: hidden;
    margin-top: 13px;
  }
}

